}

.containerADM{
    width: 100%;
    min-height: 90%;

    background-image: url(/static/media/background.ed08c809.png);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    
    justify-content: space-around;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.cardModulo{
    border: solid black 1px;
    border-radius: 20px;
    width: 20%;
    height: 18%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 1rem;
    background-color: rgb(235, 219, 181);
    padding: 10px;
}

.nomeCurso{
    margin: 0;
    text-align: center;
}

.qtdCurso{
    margin-top: 5px;
    margin-bottom: 5px;
}

.botoes{
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.abrirModulo{
    background-color: greenyellow;
    border-radius: 7px;
    height: 1.75rem;
    width: 5rem;
    border: none
}

.removerModulo{
    background-color: red;
    border-radius: 7px;
    height: 1.75rem;
    width: 5rem;
    margin-left: 4px;
    border: none
}

.abrirModulo:hover{
    background-color: green;
    font-weight: bold;
}

.removerModulo:hover{
    background-color: #CC0D0D;
    font-weight: bold;
}
